#itemListDiv{
  position: absolute;
  bottom: 0;
  right: 0;
  width: 35%;
  height: 25%;
}

#itemListContent{
  overflow-y: scroll;
  height: 70%;
  width: 96%;
  padding-left: 4%;
}


#itemListContent > ul{
  list-style: none;
  margin-top:0;
  margin-bottom:0;
  padding-left: 0;
}


