#toolBarDiv{
  position: absolute;
  left: 0;
  top: 0;
  width: 20%;

}

#toolsDiv{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-left: 5px;
  margin-top: -5px;
}

.toolDiv{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 2px;
  margin-left: 8px;
  max-width: 40px;
  margin-top: 8px;
  font-size: 10px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.toolDiv:hover{
  cursor: pointer;
}

.toolIcon{
  border: 1px solid black;
  width: 40px;
  height: 40px;
}



